body{

    background-color:	#F8F8F8;
}

.App {
    text-align: center;
  
    margin-top: 10px;
  }
  
  .App {text-align: center;
    display:flex;
  
    align-items: center;
    justify-content: center; 
     flex-wrap: wrap;
     gap: 20px;
  
  }
  
  
  ul{
    width: 300px;
    height: 350px;
    border: 1px solid transparent;
    border-radius: 7px;
    list-style: none;
    background-color:rgb(59,122,87,0.3);
    padding: 10px;
   
  }
  span{
    color: #fff;
    font-weight: bold;
  }
  
  
  .favorites_btn{
    width: 80px;
    height: 40px;
    border-radius: 7px;
  }
  li{
    margin: 5px;
  }
  li:first-child{
    margin-top: 20px;
  }
  
  .active{
    color:cornflowerblue  !important;
    cursor: default;
  }
  h2{
    color: lightgray;
  }
  h4{
    font-size: 22px;
    &:hover{
      color: cadetblue;
    }
  }